import { createMarkMobileButton } from './components/createMobileButton';

export default function createToolbar(type, Icon) {
  return function toolbar(pluginConfig) {
    const pluginLocaleConfig = pluginConfig.locale || {};
    return {
      buttons: {
        mobile: createMarkMobileButton(type, Icon, 'horiz'),
        mobileBig: createMarkMobileButton(type, Icon, 'big'),
      },
      locale: pluginLocaleConfig,
    };
  };
}
